

.loading{
    width: 100%;
    height: 100vh;
    position: absolute;
    z-index: 16;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #103D34;
    transition: all 0.5s ease;
}

.loading_logo{
    width: 200px;
}

.loading_deactivate{
    width: 100%;
    height: 100vh;
    position: absolute;
    z-index: 6;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #103D34;
    margin-top: -250%;
    transition: all 0.5s ease;
}




 @import url("https://fonts.googleapis.com/css?family=Lato:300,400|Poppins:300,400,800&display=swap");
 .container {
    width: 100%;
    height: 100vh;
    background: #232323;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .container .box {
    width: 150px;
    height: 150px;
    position: relative;
    display: flex;
    justify-content: center;
    flex-direction: column;
  }
  .container .box .title {
    width: 100%;
    position: relative;
    display: flex;
    align-items: center;
    height: 50px;
  }
  .container .box .title .block {
    width: 0%;
    height: inherit;
    background: #ffb510;
    position: absolute;
    animation: mainBlock 1s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;
    display: flex;
  }
  
  /* სათაურის ანიმაცია */
  .container .box .title h1 {
    font-family: "Poppins";
    color: #fff;
    font-size: 32px;
    -webkit-animation: mainFadeIn 1s forwards;
    -o-animation: mainFadeIn 1s forwards;
    animation: mainFadeIn 1s forwards;
    animation-delay: 1s;
    opacity: 0;
    display: flex;
    align-items: baseline;
    position: relative;
  }
  
  /* ბურთის ამოხტომა */
  .container .box .title h1 span {
    width: 0px;
    height: 0px;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    background: #ffb510;
    -webkit-animation: load 0.6s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;
    animation: popIn 0.8s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;
    animation-delay: 1.5s;
    margin-left: 5px;
    margin-top: -10px;
    position: absolute;
    bottom: 13px;
    right: -12px;
  }
  .container .box .role {
    width: 100%;
    position: relative;
    display: flex;
    align-items: center;
    height: 30px;
    margin-top: -10px;
  }
  
  /* ქვედა ხაზი */
  .container .box .role .block {
    width: 0%;
    height: inherit;
    background: #e91e63;
    position: absolute;
    animation: secBlock 1s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;
    animation-delay: 1.2s;
    display: flex;
  }
  
  /* ქვედა ტექსტი */
  .container .box .role p {
    animation: secFadeIn 1s forwards;
    animation-delay: 2s;
    opacity: 0;
    font-weight: 400;
    font-family: "Lato";
    color: #ffffff;
    font-size: 12px;
    text-transform: uppercase;
    letter-spacing: 5px;
  }
  
  @keyframes mainBlock {
    0% {
      width: 0%;
      left: 0;
    }
    50% {
      width: 100%;
      left: 0;
    }
    100% {
      width: 0;
      left: 100%;
    }
  }
  @keyframes secBlock {
    0% {
      width: 0%;
      left: 0;
    }
    50% {
      width: 100%;
      left: 0;
    }
    100% {
      width: 0;
      left: 100%;
    }
  }
  @keyframes mainFadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  @keyframes popIn {
    0% {
      width: 0px;
      height: 0px;
      background: #e9d856;
      border: 0px solid #ddd;
      opacity: 0;
    }
    50% {
      width: 10px;
      height: 10px;
      background: #e9d856;
      opacity: 1;
      bottom: 45px;
    }
    65% {
      width: 7px;
      height: 7px;
      bottom: 0px;
      width: 15px;
    }
    80% {
      width: 10px;
      height: 10px;
      bottom: 20px;
    }
    100% {
      width: 7px;
      height: 7px;
      background: #e9d856;
      border: 0px solid #222;
      bottom: 13px;
    }
  }
  @keyframes secFadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 0.5;
    }
  }
  
  
  @keyframes top {
    0% {
      opacity: 0;
      bottom: -80px;
    }
    100% {
      opacity: 1;
      bottom: 0px;
    }
  }
  @keyframes icon {
    0% {
      opacity: 0;
      transform: scale(0);
    }
    50% {
      opacity: 1;
      transform: scale(1.3) rotate(-2deg);
    }
    100% {
      opacity: 1;
      bottom: 0px;
    }
  }